:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-primary-rgb: 177, 227, 70;
    --bs-border-radius-xl: 10px;

    .bg-dark-3 {
        background-color: #2d2d2d;
    }
}

// color-homepage

// background colors
.bg-light {
    background-color: var(--tc-bg-light) !important;
}

.bg-radish {
    background-color: var(--tc-system-radish);
}
.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.content-top {
    .line {
        width: 20px;
        height: 1px;
        opacity: 0.5;
        background-color: var(--tc-theme-primary);
    }
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}

.bg-dark {
    --bs-dark-rgb: 41, 41, 41;
}

.filter-invert {
    filter: invert(1);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
